* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  transition: background-color 0.3s ease, color 0.3s ease;
  display: flex;
  justify-content: center;
  font-family: "inter", sans-serif;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}
:root {
  --bg-color: #f8f8f8;
  --text-color: #050505;
  --accent-color: #9bb537;
  --sidebar-bg-color: #9bb537;
  --details-color: #a19d9d99;
}
.dark-theme {
  --bg-color: #050505;
  --text-color: #f8f8f8;
  --accent-color: #9bb537;
  --sidebar-bg-color: #050505;
  --details-color: #ffffff99;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}
ul {
  list-style-type: none;
}
/* Header*/
.header {
  display: flex;
  margin-top: 20px;
  flex-direction: column;
  align-items: center;
}
.header-container-logo {
  display: flex;
  align-items: center;
  gap: 160px;
}
.header-icons {
  display: flex;
  gap: 14px;
  align-items: center;
}

/* Sidebar*/
.sidebar {
  display: none;
  background-color: var(--sidebar-bg-color);
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}

#open-sidebar {
  cursor: pointer;
}
#open-sidebar:hover {
  transform: scale(1.1);
  transition: all 0.3s ease;
}
.close-sidebar {
  position: relative;
  top: -240px;
  left: 131px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.sidebar-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sidebar-list {
  display: flex;
  flex-flow: column;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  color: #fff;
}

.theme-switch-sidebar {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 20px;
  bottom: -217px;
  right: 112px;
}

.theme-switch-sidebar input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 34px;
  cursor: pointer;
  transition: 0.4s;
}

.slider:before {
  content: "";
  position: absolute;
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 2px;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.4s ease;
}

input:checked + .slider {
  background-color: #cecdcd;
}

input:checked + .slider:before {
  transform: translateX(26px);
}
.theme-switch,
.header-list {
  display: none;
}
/* hero section*/
.hero-container {
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 320px;
}
.hero-title {
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.hero-title h2 {
  font-size: 26px;
  font-weight: 600;
  text-transform: uppercase;
}
.hero-title h3 {
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 20px;
}
.hero-title span {
  color: var(--accent-color);
  font-style: italic;
}

/* Order now modal*/
.order-button {
  border: 1px solid var(--accent-color);
  background-color: transparent;
  width: 111px;
  height: 37px;
  border-radius: 12px;
  color: var(--text-color);
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.4s ease;
}
.order-button:hover {
  background-color: var(--accent-color);
  color: var(--bg-color);
}
.order-now-modal {
  display: none;
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(54, 54, 54, 0.8);
  backdrop-filter: blur(5px);
  overflow: hidden;
}
.order-now-content {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #ffffff;
  border: none;
  border-radius: 15px;
  padding: 15px;

  width: 310px;
  height: 586px;

  overflow-y: hidden;
}
.order-now-content h3 {
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 20px;
  font-size: 18px;
  margin-top: 53px;
  margin-right: 144px;
  color: #050505;
}
.order-now-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: left;
  font-size: 12px;
  color: #050505;
}
.order-now-form input {
  height: 48px;
  width: 260px;
  border-radius: 15px;
  border: 1px solid #05050533;
}
.order-now-form textarea {
  height: 48px;
  width: 260px;
  border-radius: 15px;
  border: 1px solid #05050533;
}
.send-order-btn {
  width: 260px;
  height: 48px;
  color: var(--bg-color);
  background-color: var(--accent-color);
  border: none;
  cursor: pointer;
  font-weight: 500;
  border-radius: 15px;
}
.close-order-now-modal {
  position: absolute;
  top: 18px;
  right: 21px;
  cursor: pointer;
}

/* Hero */
#masterclass-list {
  display: flex;
  flex-direction: row;
  margin-top: 48px;
  gap: 20px;
}
.masterclass-item {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin-right: 0px;
}
.chef-img {
  width: 122px;
  height: 300px;
  object-fit: cover;
  border-radius: 30px;
}
.food-img {
  object-fit: cover;
  border-radius: 30px;
  width: 200px;
  height: 300px;
  position: relative;
}
.topic-name {
  text-transform: uppercase;
  color: var(--details-color);
  text-align: center;
  font-size: 10px;
  position: absolute;
  top: 267px;
  left: 129px;
  font-weight: 800;
  text-align: center;
  width: 39%;
  margin-left: 10px;
}
.swiper {
  height: 430px;
  width: 100%;
  padding: 10px;
  max-width: 340px;
  margin: 0 auto;
  overflow: hidden;
  margin-top: 32px;
  margin-bottom: 32px;
}

.swiper-wrapper {
  display: flex;
}

.swiper-slide {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 360px;
  flex-shrink: 0;
}
.swiper-pagination-bullet {
  background-color: var(--text-color);
}

.swiper-pagination-bullet-active {
  background-color: var(--accent-color);
  width: 20px;
  border-radius: 4px;
}
.swiper-pagination {
  position: absolute;
  bottom: 10px;
  margin-bottom: -12px;
}
/* Categories section */
.categories {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 50px;
  align-items: center;
}
.categories-container {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 40px;
}
.all-categories-btn {
  width: 176px;
  height: 50px;

  border-radius: 15px;
  border: 1px solid var(--accent-color);
  color: var(--details-color);
  background-color: var(--bg-color);
  font-size: 18px;
  cursor: pointer;
  margin-bottom: 24px;
  transition: background-color 0.4s ease;
}
.all-categories-btn:hover {
  background-color: var(--accent-color);
  color: var(--bg-color);
}
.category-btn {
  background-color: transparent;
  border: none;
  color: var(--details-color);
  cursor: pointer;
  transition: background-color 0.4s ease;
  font-size: 14px;
}

.category-btn:hover {
  color: var(--accent-color);
}
#categories-list {
  display: flex;
  flex-direction: column;
  height: 198px;
  overflow: scroll;
  gap: 18px;
  overflow-x: hidden;
  padding-right: 205px;
}
::-webkit-scrollbar {
  width: 8px;
}

t::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}
::-webkit-scrollbar-button {
  display: none;
}
.recipes-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.recipes-list-title {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
}
.popular-recipe-item {
  height: 67px;
  width: 332px;
  display: flex;
  gap: 16px;
}
.popular-recipes {
  max-width: 320px;
  margin-bottom: 40px;
  max-height: 227px;
  overflow: hidden;
}
.recipe-description {
  font-size: 12px;
  font-weight: 400;
  color: var(--details-color);

  display: -webkit-box;
  display: box; 
  -webkit-line-clamp: 2;
  line-clamp: 2; /* <-- standard property */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.popular-recipe-item img {
  width: 64px;
  height: 64px;
  border-radius: 8px;
}
.popular-recipe-name {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-color);
}
.details-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 252px;
}

.search-recipes {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 40px;
  align-items: center;
  max-width: 350px;
}
.search-recipes input {
  width: 335px;
  height: 46px;
  border-radius: 15px;
  border-color: var(--details-color);
  background-color: transparent;
  padding-left: 10px;
}
input::placeholder {
  color: var(--details-color);
  font-size: 14px;
  font-style: italic;
  font-weight: 500;
  padding-top: 2px;
}
.search-recipes label {
  font-size: 14px;
  color: var(--details-color);
}
.select-wrapper,
.input-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.input-wrapper {
  padding-right: 8px;
}
.recipe-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  margin-left: 5px;
}

.search-recipes-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: baseline;
}

/* Choices.js */
/* Customize the dropdown input */
.choices {
  flex: 1 1 30%;
  width: 160px;
  min-width: 160px;
  height: 46px;
  min-height: 46px;
  max-height: 49px;
  border-radius: 15px;
  border-color: var(--details-color);
  background-color: transparent;
}
.choices__list--dropdown {
  max-height: 200px;
  border-radius: 5px;
  overflow: hidden;
}

.choices__item--choice {
  padding: 8px 12px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  font-size: 1rem;
  color: var(--accent-color);
  width: 300px;
}
.choices__inner {
  color: var(--text-color);
  border: 2px solid #ddd;
  background: var(--bg-color);
  transition: 0.4s;
  border-radius: 15px;
  cursor: pointer;
  max-height: 46px;
  display: flex;
  align-items: center;
}
.choices.is-focused .choices__inner,
.choices.is-open .choices__inner {
  border-radius: 15px;
}

/* Placeholder text */
.choices__placeholder {
  color: #999;
  font-style: italic;
}

/* Dropdown items */
.choices__item--selectable {
  padding: 6px 10px;
  border-radius: 4px;
}

/* Selected item styling */
.choices__item--choice.is-highlighted {
  background-color: var(--bg-color);
  color: var(--accent-color);
}

/* Tablet */
@media screen and (min-width: 768px) {
  #open-sidebar {
    display: none;
  }
  .header-list {
    display: flex;
    gap: 15px;
    font-size: 12px;
    color: var(--text-color);
  }
  .header-list a {
    transition: transform 0.4s ease;
  }
  .header-list a:hover {
    color: var(--accent-color);
  }
  .theme-switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 20px;
  }
  .theme-switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    transition: background-color 0.4s ease;
  }

  .slider:before {
    transition: transform 0.4s ease;
  }
  input:checked + .slider {
    background-color: var(--accent-color);
  }
  /* hero section*/
  .hero-container {
    max-width: 700px;
    width: 58%;
    margin-left: 82px;
  }
  .hero-title h2 {
    font-size: 36px;
  }
  .hero-title h3 {
    font-size: 16px;
  }
  /* Order now modal*/

  .order-now-content {
    width: 400px;
    height: 586px;
  }
  /* Swiper */
  .swiper {
    max-width: 740px;
  }
  #masterclass-list {
    gap: 0px;
  }
  .topic-name {
    left: 190px;
  }
  /* Categories section */

  .categories {
    align-items: normal;
    flex-direction: row;
    gap: 32px;
  }
  .category-btn {
    font-size: 16px;
  }
  .popular-recipes {
    max-height: 100%;
  }
  #categories-list {
    height: 400px;
  }
  .popular-recipe-item img {
    width: 48px;
    height: 48px;
  }
  .recipes-list {
    gap: 16px;
  }
  .categories-wrapper {
    margin-top: 60px;
  }
}

/* Desktop */
@media screen and (min-width: 1280px) {
  .header-list {
    font-size: 14px;
  }
  .header-container-logo {
    gap: 429px;
  }
  /* hero section*/
  .hero-container {
    max-width: 1200px;
    width: 100%;
    gap: 21px;
    margin-top: 38px;
    margin-left: 50px;
    flex-direction: row;
    align-items: center;
  }
  .chef-img {
    width: 182px;
    height: 455px;
  }
  .food-img {
    width: 351px;
    height: 455px;
  }
  #masterclass-list {
    gap: 250px;
  }
  .topic-name {
    top: 364px;
    left: 168px;
    width: 50%;
    font-size: 14px;
    font-weight: 500;
  }
  .hero-title {
    width: 66%;
  }
  /* Swiper */
  .swiper {
    max-width: 1200px;
  }

  .swiper {
    height: 490px;
  }
  /* Categories section */
  .search-recipes-container {
    flex-direction: row;
  }
  .recipe-filters {
    flex-wrap: nowrap;
  }
  .search-recipes {
    max-width: 1200px;
    align-items: baseline;
  }
  .categories {
    padding: 50px;
  }
  .recipe-filters select {
    width: 125px;
    height: 48px;
  }
  .search-recipes input {
    width: 278px;
    height: 48px;
  }
  .search-recipes-container {
    grid-area: 16px;
  }
}
